  id: 2,
  ttl: '<span>H</span>TML5Canvas<br class="u-sp"><br class="u-sp">',
  txt: "HTML5Canvas<br><br>",
  alt: "HTML5Canvas",
  p1: "Canvas",
  p1_color: "js-yellow",
  p1_list1: "<span data='dot'>&#9642;</span>JavaScript15.getContext('2d')HTML5CanvasCanvas.getContext('2d')",
  p1_list2: "<span data='dot'>&#9642;</span>c()c()",
  p2: '<br class="u-sp">.beginPath()',
  p2_color: "js-yellow",
  p2_list1: "<span data='dot'>&#9642;</span>.beginPath()beginPath()",
  p2_list2: "<span data='dot'>&#9642;</span><a href='https://developer.mozilla.org/ja/docs/Web/API/CanvasRenderingContext2D' target='_blank'>MDN</a>",
  p3: '',
  p3_color: "js-yellow",
  p3_list1: "<span data='dot'>&#9642;</span>JavaScript46fori88",
  p3_list2: "<span data='dot'>&#9642;</span>JavaScript65x = x + 100;x1100px",
  func
