/**
 * js--------------------------------------
 */

function func() {

  //canvas
  let canvasParent = document.getElementById("canvasParent");
  let canvas = document.getElementById("canvas");

  // Canvasif
  if (canvas.getContext) {

    // canvas
    canvas.width = canvasParent.clientWidth;
    canvas.height = canvasParent.clientHeight;

    // Canvas
    let c = canvas.getContext('2d');

    // 
    c.fillStyle = "rgb(0, 0, 255)";
    c.fillRect(50, 50, 50, 50);
    c.fillStyle = "#ffff00";
    c.fillRect(150, 50, 50, 50);
    c.fillStyle = "green";
    c.fillRect(250, 50, 50, 50);

    // 
    if (window.innerWidth > 750) {
      var line1 = {
        x: 50,
        y: 200,
      };
      var line2 = {
        x: 100,
        y: 250,
      };
      var line3 = {
        x: 150,
        y: 200,
      };
      var line4 = {
        x: 200,
        y: 250,
      };
      var line5 = {
        x: 250,
        y: 200,
      };
    } else {
      var line1 = {
        x: 50,
        y: 150,
      };
      var line2 = {
        x: 100,
        y: 200,
      };
      var line3 = {
        x: 150,
        y: 150,
      };
      var line4 = {
        x: 200,
        y: 200,
      };
      var line5 = {
        x: 250,
        y: 150,
      };
    }
    c.beginPath();
    c.moveTo(line1.x, line1.y);
    c.lineTo(line2.x, line2.y);
    c.lineTo(line3.x, line3.y);
    c.lineTo(line4.x, line4.y);
    c.lineTo(line5.x, line5.y);
    c.strokeStyle = 'red';
    c.stroke();

    // 
    if (window.innerWidth > 750) {
      var circle = {
        x: 100,
        y: 380,
        radius: 30,
        startAngle: 0,
        endAngle: Math.PI * 2,
        anticlockwise: false
      }
    } else {
      var circle = {
        x: 80,
        y: 270,
        radius: 30,
        startAngle: 0,
        endAngle: Math.PI * 2,
        anticlockwise: false
      }
    }
    c.beginPath();
    c.arc(circle.x, circle.y, circle.radius, circle.startAngle, circle.endAngle, circle.anticlockwise, );
    c.fillStyle = "skyblue";
    c.fill();
    c.strokeStyle = "rgb(70, 0, 128)";
    c.stroke();

    // 
    if (window.innerWidth > 750) {
      var triangle = {
        x: 100,
        y: 550,
        diffX: 30,
        diffY: 50,
        space: 100
      }
    } else {
      var triangle = {
        x: 40,
        y: 360,
        diffX: 15,
        diffY: 25,
        space: 40
      }
    }
    for (var i = 0; i < 8; i++) {
      c.beginPath();
      c.moveTo(triangle.x, triangle.y - triangle.diffY);
      c.lineTo(triangle.x - triangle.diffX, triangle.y);
      c.lineTo(triangle.x + triangle.diffX, triangle.y);
      c.lineTo(triangle.x, triangle.y - triangle.diffY);
      c.fillStyle = 'turquoise';
      c.fill();
      triangle.x = triangle.x + triangle.space;
    }

  }

}
