export default {
  id: 9,
  ttl: "<span>C</span>anvas<br class='u-sp'><br>PC",
  txt: "Canvas<br><br>PC",
  alt: "CanvasPC",
  p1: "<br class='u-sp'>",
  p1_color: "js-yellow",
  p1_list1: "<span data='dot'>&#9642;</span>JavaScript6468x='0'y='1/2'(62)forcanvas.widthpxx1pxMath.sin()y",
  p1_list2: "<span data='dot'>&#9642;</span>",
  p2: "<br class='u-sp'>",
  p2_color: "js-yellow",
  p2_list1: "<span data='dot'>&#9642;</span>JavaScript5358c.fillRect(0.01)",
  p2_list2: "<span data='dot'>&#9642;</span>",
  p3: "",
  p3_color: "js-yellow",
  p3_list1: "<span data='dot'>&#9642;</span>JavaScript7072(hsl)h0&lt;h&lt;200",
  p3_list2: "<span data='dot'>&#9642;</span>Math.sin()hh()Math.abs()<br><br><a href='https://developer.mozilla.org/ja/docs/Web/JavaScript/Reference/Global_Objects/Math/abs' target='_blank'>Math.abs()</a>",
  func
}


/**
 * js--------------------------------------
 */

function func() {

  //canvas
  let canvasParent = document.getElementById("canvasParent");
  let canvas = document.getElementById("canvas");

  // Canvasif
  if (canvas.getContext) {

    // canvas
    canvas.width = canvasParent.clientWidth;
    canvas.height = canvasParent.clientHeight;

    // Canvas
    let c = canvas.getContext('2d');

    // 
    const wave = {
      y: canvas.height / 2,
      // 
      length: 0.01,
      // 
      amplitude: 100,
      // 
      frequency: 0.01
    }

    // 
    const strokeColor = {
      h: 200,
      s: 50,
      l: 50
    }

    // 
    const backgroundColor = {
      r: 0,
      g: 0,
      b: 0,
      a: 0.01
    }

    // 
    let increment = wave.frequency;

    // 
    function animate() {

      // 
      requestAnimationFrame(animate)

      // 
      c.fillStyle = `rgba(${backgroundColor.r},${backgroundColor.g},
        ${backgroundColor.b},${backgroundColor.a})`;
      c.fillRect(0, 0, canvas.width, canvas.height);

      // 
      c.beginPath();
      c.moveTo(0, canvas.height / 2);

      // 
      for (let i = 0; i < canvas.width; i++) {
        c.lineTo(i, wave.y +
          Math.sin(i * wave.length + increment) * wave.amplitude * Math.sin(increment))
      }

      // (hsl)h0<h<200
      c.strokeStyle = `hsl(${Math.abs(strokeColor.h * Math.sin(increment))},${strokeColor.s}%,${strokeColor.l}%)`
      c.stroke();
      increment += wave.frequency;
    }

    // 
    animate()

  }

}
