export default {
  id: 1,
  ttl: '<a><span></span></a><a><span></span></a><br class="u-sp">',
  txt: "<br><br>",
  alt: "",
  p1: "point1",
  p1_color: "js-orange",
  p1_list1: "<span data='dot'>&#9642;</span>1",
  p1_list2: "<span data='dot'>&#9642;</span>2",
  p2: 'point2',
  p2_color: "js-blue",
  p2_list1: "<span data='dot'>&#9642;</span>1",
  p2_list2: "<span data='dot'>&#9642;</span>2",
  p3: 'point3',
  p3_color: "js-yellow",
  p3_list1: "<span data='dot'>&#9642;</span>1",
  p3_list2: "<span data='dot'>&#9642;</span>2",
  func
}

/**
 * js--------------------------------------
 */

function func() {

  //.area
  var area = document.querySelector(".area");

  //mousemovetouchmove
  var action = (e) => {

    // .area
    var clientRect = area.getBoundingClientRect();

    // .area
    var areaTop = clientRect.top;

    // .area
    var areaLeft = clientRect.left;

    // 
    if (window.innerWidth > 750) {
      var distanceTop = e.clientY;
      var distanceLeft = e.clientX;
    } else {
      var distanceTop = e.changedTouches[0].pageY;
      var distanceLeft = e.changedTouches[0].pageX;
    }


    // .area
    var distanceY = distanceTop - areaTop;
    var distanceX = distanceLeft - areaLeft;

    // .overlaytopleft
    $('.overlay').css({
      "top": distanceY + 'px',
      "left": distanceX + 'px'
    });
  }


  // PCmousemoveSPtouchmove
  if (window.innerWidth > 750) {
    area.addEventListener("mousemove", (e) => {
      action(e);
    });
  } else {
    area.addEventListener("touchmove", (e) => {
      action(e);
      // 
      event.preventDefault();
    });
  }

}
