  ttl: '<span>H</span>TML5Canvas<br class="u-sp"><br class="u-sp">',
  txt: "HTML5Canvas<br><br>",
  alt: "HTML5Canvas",
  p1: "<br class='u-sp'>",
  p1_color: "js-yellow",
  p1_list1: "<span data='dot'>&#9642;</span><span class='u-pcin'></span><span class='u-spin'></span><span class='u-pcin'></span><span class='u-spin'></span><a href='./page4.html' target='_blank'></a><br><br><br><b><span class='u-pcin'></span><span class='u-spin'></span></b><br>(JavaScript2434)<br><br><b><span class='u-pcin'></span><span class='u-spin'></span></b><br>(JavaScript8592)<br><br><b></b><br>(JavaScript9395)",
  p1_list2: "",
  p2: "<br class='u-sp'><span class='u-pcin'></span><span class='u-spin'></span>",
  p2_color: "js-yellow",
