/**
 * js--------------------------------------
 */

function func() {
  //canvas
  let canvasParent = document.getElementById("canvasParent");
  let canvas = document.getElementById("canvas");

  // Canvasif
  if (canvas.getContext) {

    // canvas
    canvas.width = canvasParent.clientWidth;
    canvas.height = canvasParent.clientHeight;

    // Canvas
    let c = canvas.getContext('2d');

    // &
    let val = {
      quantity: 50,
      maxRadius: 20,
      colorArray: [
        '#248EA6',
        '#84BFA4',
        '#F2E74B',
        '#F2D49B',
        '#D94141',
      ],
      gravity: 1,
      friction: .8,
    }

    //
    function Ball(radius, x, y, vX, vY, color) {
      this.radius = radius;
      this.x = x;
      this.y = y;
      this.firstY = this.y;
      this.vX = vX;
      this.vY = vY;
      // (this.vY(125))
      this.firstVY = this.vY * 2;
      // colorArray
      this.color = color;

      // 
      this.draw = () => {
        c.beginPath();
        c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false);
        c.fillStyle = this.color;
        c.fill();
      }

      // 
      this.reset = () => {
        this.vY = this.firstVY;
      }

      // 
      this.update = () => {

        // 
        this.x += this.vX;
        this.y += this.vY;

        // 
        if (this.y + this.radius + this.vY > canvas.height) {
          this.vY = -this.vY * val.friction;
        } else {
          this.vY += val.gravity;
        }

        // x
        if (this.x + this.radius > canvas.width || this.x - this.radius < 0) {
          this.vX = -this.vX;
        }

        // 
        this.draw();
      }

    }

    // 
    var ballArray = []

    for (let i = 0; i < val.quantity; i++) {
      // 
      // 
      var radius = Math.random() * val.maxRadius;
      // x
      var x = Math.random() * (canvas.width - radius * 2) + radius;
      // y(y)
      var y = Math.random() * (canvas.height / 2 - radius * 2) + radius;
      // x()
      var vX = (Math.random() - 0.5) * 2;
      // y(&10)
      var vY = Math.random() * 10 + 10;
      // 
      var color = val.colorArray[Math.floor(Math.random() * val.colorArray.length)];

      //
      ballArray.push(new Ball(radius, x, y, vX, vY, color));
    }

    // &
    function animate() {

      // animate
      requestAnimationFrame(animate);

      // 
      c.clearRect(0, 0, canvas.width, canvas.height);

      //ballArray
      for (let i = 0; i < ballArray.length; i++) {
        ballArray[i].update();
      }

    }

    // animate
    animate();

    // 
    canvas.addEventListener("click", (event) => {
      for (let i = 0; i < ballArray.length; i++) {
        ballArray[i].reset();
      }
    })

  }






}
