import {
  unique
} from "jquery";

export default {
  id: 7,
  ttl: "<span>C</span>anvas<br class='u-sp'><br class='u-sp'>",
  txt: "Canvas<br><br>",
  alt: "Canvas",
  p1: "Math.atan2()",
  p1_color: "js-yellow",
  p1_list1: "JavaScript59Math.atan2()2",
  p1_list2: "<span data='dot'>&#9642;</span><a href='https://developer.mozilla.org/ja/docs/Web/JavaScript/Reference/Global_Objects/Math/atan2' target='_blank'>Math.atan2()</a>",
  p2: "",
  p2_color: "",
  p2_list1: "",
  p2_list2: "",
  p3: "",
  p3_color: "",
  p3_list1: "",
  p3_list2: "",
  func
}


/**
 * js--------------------------------------
 */

function func() {

  //canvas
  let canvasParent = document.getElementById("canvasParent");
  let canvas = document.getElementById("canvas");

  // Canvasif
  if (canvas.getContext) {

    // canvas
    canvas.width = canvasParent.clientWidth;
    canvas.height = canvasParent.clientHeight;

    // Canvas
    let c = canvas.getContext('2d');

    // ------------------------------------------------------------------

    // &
    let val = {
      quantity: 50,
      radius: 20,
      colorArray: [
        ["#F27EBE", "#fff5fa"],
        ["#3DF2BF", "#e6fff8"],
        ["#05AFF2", "#def6ff"],
        ["#F2E085", "#fffae0"],
        ["#F24822", "#ffd1c7"],
      ],
      velocityRange: 5,
      strokeWidth: 2
    }

    // ------------------------------------------------------------------

    // 

    //
    function rotate(velocity, angle) {
      const rotatedVelocities = {
        x: velocity.x * Math.cos(angle) - velocity.y * Math.sin(angle),
        y: velocity.x * Math.sin(angle) + velocity.y * Math.cos(angle)
      }
      return rotatedVelocities;
    }

    //
    function resolveCollision(particle, otherParticle) {
      const xVelocityDiff = particle.velocity.x - otherParticle.velocity.x;
      const yVelocityDiff = particle.velocity.y - otherParticle.velocity.y;

      const xDist = otherParticle.x - particle.x;
      const yDist = otherParticle.y - particle.y;

      // Prevent accidental overlap of particles
      if (xVelocityDiff * xDist + yVelocityDiff * yDist >= 0) {

        //Grab angle between the Two colliding particles
        const angle = -Math.atan2(otherParticle.y - particle.y, otherParticle.x - particle.x)

        //Store mass invar for better readability in collision equation
        const m1 = particle.mass;
        const m2 = otherParticle.mass;

        //Velocity before equation
        const u1 = rotate(particle.velocity, angle);
        const u2 = rotate(otherParticle.velocity, angle);


        //Velocity after 1d collision equation
        const v1 = {
          x: u1.x * (m1 - m2) / (m1 + m2) + u2.x * 2 * m2 / (m1 + m2),
          y: u1.y
        };
        const v2 = {
          x: u2.x * (m1 - m2) / (m1 + m2) + u1.x * 2 * m2 / (m1 + m2),
          y: u2.y
        };


        //Final velocity after rotating axis back to original location
        const vFinal1 = rotate(v1, -angle);
        const vFinal2 = rotate(v2, -angle);

        //Swap particle velocities for realistic bounce effect
        particle.velocity.x = vFinal1.x;
        particle.velocity.y = vFinal1.y;

        otherParticle.velocity.x = vFinal2.x;
        otherParticle.velocity.y = vFinal2.y;
      }
    }

    // 
    function randomIntFromRange(area, radius) {
      return Math.floor(Math.random() * (area - radius + 1) + radius);
    }

    // 
    function distance(x1, y1, x2, y2) {
      const xDist = x2 - x1;
      const yDist = y2 - y1;
      return Math.sqrt(Math.pow(xDist, 2) + Math.pow(yDist, 2));
    }

    // ------------------------------------------------------------------

    //
    function Particle(x, y, radius) {
      this.x = x;
      this.y = y;
      this.velocity = {
        x: (Math.random() - 0.5) * val.velocityRange,
        y: (Math.random() - 0.5) * val.velocityRange
      }
      this.radius = radius;
      this.mass = 1;
      // colorArray
      this.color = val.colorArray[Math.floor(Math.random() * val.colorArray.length)];
      this.stroke = this.color[0];
      this.fill = this.color[1];

      // 
      this.draw = () => {
        c.beginPath();
        c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false);
        c.fillStyle = this.fill;
        c.fill();
        c.lineWidth = val.strokeWidth;
        c.strokeStyle = this.stroke;
        c.stroke();
      }

      // 
      this.update = particles => {

        // 
        this.draw();

        //
        for (let i = 0; i < particles.length; i++) {
          if (this === particles[i]) continue;
          if (distance(this.x, this.y, particles[i].x, particles[i].y) - this.radius * 2 < 0) {
            resolveCollision(this, particles[i]);
          }
        }

        // 
        if (this.x + this.radius > canvas.width || this.x - this.radius < 0) {
          this.velocity.x = -this.velocity.x;
        } else if (this.y + this.radius > canvas.height || this.y - this.radius < 0) {
          this.velocity.y = -this.velocity.y;
        }

        // 
        this.x += this.velocity.x;
        this.y += this.velocity.y;

      }

    }

    // ------------------------------------------------------------------

    // 
    let particles;

    // 
    function init() {

      // 
      particles = []

      // 
      for (let i = 0; i < val.quantity; i++) {
        // 
        const radius = val.radius;
        // x
        let x = randomIntFromRange(canvas.width, radius);
        // y
        let y = randomIntFromRange(canvas.height, radius);

        // 
        if (i !== 0) {
          for (let j = 0; j < particles.length; j++) {
            if (distance(x, y, particles[j].x, particles[j].y) - radius * 2 < 0) {
              // 
              x = randomIntFromRange(canvas.width, radius);
              y = randomIntFromRange(canvas.height, radius);

              // jj1
              j = -1;
            }
          }
        }

        //
        particles.push(new Particle(x, y, radius));
      }

    }

    // ------------------------------------------------------------------

    // &
    function animate() {

      // animate
      requestAnimationFrame(animate);

      // 
      c.clearRect(0, 0, canvas.width, canvas.height);

      // update
      particles.forEach(particle => {
        particle.update(particles);
      });

    }

    // ------------------------------------------------------------------

    // init
    init();
    // animate
    animate();

  }

}
