export default {
  id: 8,
  ttl: "<span>C</span>anvas<br class='u-sp'><br class='u-sp'>",
  txt: "Canvas<br><br>",
  alt: "Canvas",
  p1: "Math.cos(sin)radians",
  p1_color: "js-yellow",
  p1_list1: "<span data='dot'>&#9642;</span>(JavaScript7299100)",
  p1_list2: "<span data='dot'>&#9642;</span><a href='https://developer.mozilla.org/ja/docs/Web/JavaScript/Reference/Global_Objects/Math/cos' target='_blank'>Math.cos()</a>",
  p2: "<br class='u-sp'>",
  p2_color: "js-yellow",
  p2_list1: "<span data='dot'>&#9642;</span>JavaScript1451(0.05)",
  p2_list2: "",
  p3: "",
  p3_color: "",
  p3_list1: "",
  p3_list2: "",
  func
}


/**
 * js--------------------------------------
 */

function func() {

  //canvas
  let canvasParent = document.getElementById("canvasParent");
  let canvas = document.getElementById("canvas");

  // Canvasif
  if (canvas.getContext) {

    // canvas
    canvas.width = canvasParent.clientWidth;
    canvas.height = canvasParent.clientHeight;

    // Canvas
    let c = canvas.getContext('2d');

    // canvas
    var canvasSize = canvas.getBoundingClientRect();
    // canvas
    var canvasLeft = canvasSize.left;
    // canvas
    var canvasTop = canvasSize.top;
    // 
    var windowS = window.scrollY;

    // 
    const mouse = {
      x: canvas.width / 2,
      y: canvas.height / 2
    }

    // 
    canvas.addEventListener("mousemove", (event) => {
      mouse.x = event.pageX - canvasLeft;
      mouse.y = event.pageY - canvasTop - windowS;
    })


    // ------------------------------------------------------------------

    // &
    let val = {
      quantity: 60,
      maxRadius: 4,
      velocity: 0.05,
      inertia: 0.05,
      colorArray: [
        "#00E1FF",
        "#FFFB00",
        "#FFB700",
        "#FF8800",
        "#FF510D",
      ]
    }

    // ------------------------------------------------------------------

    // 

    // // 
    function randomIntFromRange(area, radius) {
      return Math.floor(Math.random() * (area - radius + 1) + radius);
    }

    // ------------------------------------------------------------------

    // 

    function Particle(x, y, radius) {
      this.x = x;
      this.y = y;
      this.velocity = val.velocity;
      this.radius = radius;
      // 2(360)
      this.radians = Math.random() * Math.PI * 2;
      // colorArray
      this.color = val.colorArray[Math.floor(Math.random() * val.colorArray.length)];
      // 
      if (window.innerWidth > 750) {
        this.distanceFromCenter = randomIntFromRange(50, 130);
      } else {
        this.distanceFromCenter = randomIntFromRange(20, 60);
      }
      // 
      this.lastMouse = {
        x: x,
        y: y
      }

      // 
      this.update = () => {

        const lastPoint = {
          x: this.x,
          y: this.y
        };

        // (velocity)
        this.radians += this.velocity;

        //
        this.lastMouse.x += (mouse.x - this.lastMouse.x) * val.inertia;
        this.lastMouse.y += (mouse.y - this.lastMouse.y) * val.inertia;

        // 
        this.x = this.lastMouse.x + Math.cos(this.radians) * this.distanceFromCenter;
        this.y = this.lastMouse.y + Math.sin(this.radians) * this.distanceFromCenter;

        // 
        this.draw(lastPoint);

      }

      // 
      this.draw = lastPoint => {
        c.beginPath();
        c.strokeStyle = this.color;
        c.lineWidth = this.radius;
        c.moveTo(lastPoint.x, lastPoint.y);
        c.lineTo(this.x, this.y);
        c.stroke();
        c.closePath();
      }

    }


    // ------------------------------------------------------------------

    // 
    let particles;

    // 
    function init() {
      particles = [];

      for (let i = 0; i < val.quantity; i++) {
        const radius = (Math.random() * val.maxRadius) + 1;
        particles.push(new Particle(canvas.width / 2, canvas.height / 2, radius));
      }

    }

    // ------------------------------------------------------------------

    // &
    function animate() {

      // animate
      requestAnimationFrame(animate);

      // 
      c.fillRect(0, 0, canvas.width, canvas.height);

      //
      c.fillStyle = 'rgba(255, 255, 255, 0.05)';

      particles.forEach(particle => {
        particle.update();
      });

    }

    // ------------------------------------------------------------------

    // init
    init();
    // animate
    animate();

  }

}
