/**
 * js--------------------------------------
 */

function func() {
  //canvas
  let canvasParent = document.getElementById("canvasParent");
  let canvas = document.getElementById("canvas");

  // Canvasif
  if (canvas.getContext) {

    // canvas
    canvas.width = canvasParent.clientWidth;
    canvas.height = canvasParent.clientHeight;

    // Canvas
    let c = canvas.getContext('2d');

    //
    function Circle(x, y, speedX, speedY, radius) {
      this.x = x;
      this.y = y;
      this.speedX = speedX;
      this.speedY = speedY;
      this.radius = radius;

      // 
      this.draw = () => {
        c.beginPath();
        c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false);
        c.strokeStyle = 'turquoise';
        c.stroke();
      }

      // 
      this.update = () => {

        // 
        if (this.x + this.radius > canvas.width || this.x - this.radius < 0) {
          this.speedX = -this.speedX;
        } else if (this.y + this.radius > canvas.height || this.y - this.radius < 0) {
          this.speedY = -this.speedY;
        }

        // 
        this.x += this.speedX;
        this.y += this.speedY;

        // 
        this.draw();
      }

    }

    // 
    var circleArray = []

    for (let i = 0; i < 100; i++) {
      // 
      // 
      var radius = Math.random() * 50;
      // x
      var x = Math.random() * (canvas.width - radius * 2) + radius;
      // y
      var y = Math.random() * (canvas.height - radius * 2) + radius;
      // x
      var speedX = (Math.random() - 0.5) * 10;
      // y
      var speedY = (Math.random() - 0.5) * 10;

      //
      circleArray.push(new Circle(x, y, speedX, speedY, radius));
    }

    console.log(circleArray);

    // &
    function animate() {

      // animate
      requestAnimationFrame(animate);

      // 
      c.clearRect(0, 0, canvas.width, canvas.height);

      //circleArray
      for (let i = 0; i < circleArray.length; i++) {
        circleArray[i].update();
      }

    }

    // animate
    animate();

  }






}
