export default {
  id: 22,
  ttl: "<span>C</span>anvas<br class='u-sp'>HTML5",
  txt: "Canvas<br><br>HTML5",
  alt: "Canvas",
  p1: "",
  p1_color: "js-yellow",
  p1_list1: "<span data='dot'>&#9642;</span>",
  p1_list2: "<span data='dot'>&#9642;</span>",
  p2: "",
  p2_color: "js-yellow",
  p2_list1: "<span data='dot'>&#9642;</span>",
  p2_list2: "<span data='dot'>&#9642;</span>",
  p3: "",
  p3_color: "js-yellow",
  p3_list1: "<span data='dot'>&#9642;</span>",
  p3_list2: "<span data='dot'>&#9642;</span>",
  func
}



/**
 * js--------------------------------------
 */



function func() {

  // p5.jsnode module
  const p5 = require('p5');

  //
  const parent = document.getElementById("p5Parent");

  // 
  let cW = parent.clientWidth;
  let cH = parent.clientHeight;

  // (canvas)
  let quantity = Math.floor(window.innerWidth / 10);

  // 
  let ptcArray = [];

  // -------------------------------

  /**
   * 
   */
  const sketch = (p) => {

    /**
     * 1
     */
    p.setup = () => {

      // 
      let canvas = p.createCanvas(cW, cH);

      //class
      canvas.class('p5Canvas');

      // 
      p.noStroke();

      // Particle()
      for (let i = 0; i < quantity; i++) {
        ptcArray.push(new Particle());
      }

    } //p.setup()

    // ------------------------------

    /**
     * 
     */
    p.draw = () => {

      // 
      p.background('rgb(237, 255, 194)');

      // 
      ptcArray.forEach(function (el, index) {
        el.update();
        el.draw();
      });

    } // p.draw()

    // ------------------------------

    /**
     * 
     */
    class Particle {
      constructor() {
        // 
        this.pos = p.createVector(p.random(p.width), p.random(p.height));
        // 
        this.r = p.random(5, 25);
        // 
        this.v = p.createVector(p.random(-2, 2), p.random(-2, 2));
      }

      // 
      update() {
        this.pos.add(this.v);
        this.reflection();
      }

      // 
      draw() {
        // 
        let g = p.random(127, 255);
        let r = p.random(g);
        let b = p.random(b);
        p.fill(r, g, b);

        // 
        p.ellipse(this.pos.x, this.pos.y, this.r, this.r);
      }

      // 
      reflection() {
        if (this.pos.x < 0 || this.pos.x > p.width) {
          this.v.x *= -1;
        }
        if (this.pos.y < 0 || this.pos.y > p.height) {
          this.v.y *= -1;
        }
      }

    } // class Particle

  } // sketch()

  // sketch2setup()
  // canvas.parent(parent);
  new p5(sketch, parent);


}
